// color
$Very-Dark-Blue: hsl(235, 21%, 11%);
$Very-Dark-Desaturated-Blue: hsl(235, 24%, 19%);
$Light-Grayish-Blue: hsl(234, 39%, 85%);
$Dark-Grayish-Blue: hsl(234, 11%, 52%);
$Very-Dark-Grayish-Blue: hsl(233, 14%, 35%);

// ========== container ==========
.container {
  margin: 0 350px ;
}
@media(max-width: 1000px) {
  .container {
    margin: 0 100px;
  }
}
@media(max-width: 670px) {
  .container {
    margin: 0 20px;
  }
}

// ========== App ==========
.App-logo {
  h1 {
    font-weight: 700;
    letter-spacing: 10px;
    margin: 0;

    a {
      text-decoration: none;
      color: inherit;
    }
  }
}
@media(max-width: 670px) {
  .App-logo {
    h1 {
      font-size: 26px;
    }
  }
}

// ========== AppHeader ==========
.AppHeader-wrapper {
  height: 150px;
  color: #fff;

  .AppHeader {
    line-height: 170px;
  }
}
@media(max-width: 670px) {
  .AppHeader-wrapper {
    height: 110px;

    .AppHeader {
      line-height: 140px;
    }
  }
}


// ========== AddBook, EditBook ==========
.form-container {  
  padding: 30px;
}

// ========== AddBookForm ==========
.AddBookForm {
  margin: 20px 0 5px;
}

// ========== BookList ==========
.BookList {
  // ========== BookListUncompleted, BookListCompleted ==========
  .BookList-container {
    margin: 0 0 30px;

    h2 {
      margin: 0;
      background: linear-gradient(90deg, #ffffff 15%, #FC00FF 100%);
      background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    .BookList {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      list-style: none;
      list-style-position: outside;
      
      .Book {
        flex-basis: 30%;
        // max-width: 50%;
        padding: 15px;
        margin: 30px 20px;
        border-radius: 30px;
        color: #fff;
        background-color: $Very-Dark-Grayish-Blue;
        
        .Book-img {
          min-height: 200px;
          text-align: center;
          margin: -15px -15px 15px;
          border-top-left-radius: 30px;
          border-top-right-radius: 30px;
          background-color: $Dark-Grayish-Blue;

          img {
            width: 100%;
            border-top-left-radius: 16px;
            border-top-right-radius: 16px;
          }
        }

        .Book-header {
          text-align: center;

          h3 {
            margin: 10px 0;
          }
        }

        .Book-button-wrapper {
          display: flex;
          justify-content: center;

          button {
            margin: 0 10px;
            background-color: $Dark-Grayish-Blue;
          }
        }
      }
    }
  }
}
